import * as React from "react";
import type { HeadFC } from "gatsby";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Badge from "react-bootstrap/Badge";
import Button from "react-bootstrap/Button";
import { graphql, Link } from "gatsby";
import Item from "../page/Item";
import Form from "react-bootstrap/Form";
import { WrappedParse as Parse } from "../vendor/ParseApp";
import Table from "react-bootstrap/Table";
import { timeSince } from "../helpers/time";
import { handleLocation } from "../helpers/data";
import AppContext from "../context/AppContext";
import { useExternalScript } from "../helpers/hooks/useExternalScript";
import LongCard from "../components/card/LongCard";

import Alert from "react-bootstrap/Alert";
import {
  Trans,
  useTranslation,
  I18nextContext,
} from "gatsby-plugin-react-i18next";

const handleData = (key, e) => {
  if (Array.isArray(e)) {
    return e.map((obj) => <Badge> {obj.label} </Badge>);
  }

  if (key === "docUrl") {
    return (
      <a className="color-brand-1" href={e}>
        Download
      </a>
    );
  }

  if (Object.prototype.toString.call(e) === "[object Date]") {
    return timeSince(e);
  }

  if (typeof e === "string") {
    return <Badge> {e}</Badge>;
  }
};

const TemplateComponent = ({ data }) => {
  const { t } = useTranslation();
  const dataModel = data.get("data");
  const location = dataModel?.location.map((loc) => loc.label).join(", ");
  const jobs = dataModel?.tags.map((loc) => ({ label: loc.label}))
  const citizenship = dataModel.citizenship[0]?.label;

  return (
    <LongCard
      title={`Seeking to work in ${location}`}
      subtitle1={citizenship}
      subtitle2={location}
      subtitle3={dataModel?.expectSalary || ""}
      hideAvatar={true}
      badges={jobs}
      iconAction={
        <>
          <a href={dataModel?.docUrl || '#'}>
            <i className="bi bi-cloud-download fs-18"></i>
          </a>

          <a href={dataModel?.docUrl || '#'}>
            <i className="bi bi-send"></i>
          </a>
        </>
      }
    />
  );
};


const IndexPage = () => {
  const [items, setItems] = React.useState([]);

  const [hasProfile, setHasProfile] = React.useState(null);
  const [queryWord, setQueryWord] = React.useState("");
  const { t } = useTranslation();

  const context = React.useContext(AppContext);
  const i18context = React.useContext(I18nextContext);
  React.useEffect(() => {
    context.showLoading(true);
    Parse.Cloud.run("searchApplication").then((items) => {
      setItems(items);
      context.showLoading(false);
    });

    Parse.Cloud.run("getMyProfile").then((profile) => {
      setHasProfile(!!profile);
    });
  }, []);
  const handleData = (data) => {};

  const queryApplication = (queryWord) => {
    context.showLoading(true);
    Parse.Cloud.run("searchApplication", { queryWord }).then((items) => {
      setItems(items);
      context.showLoading(false);
    });
  };
  return (
    <Container className="">
      <h3> {t("searchapplication")}</h3>
      <Row>
        <Col>
          {!hasProfile && (
            <Alert key={"primary"} variant={"primary"}>
              {t("searchNotEnabled")}
              <ol>{!hasProfile && <li> {t("noprofile")}</li>}</ol>
            </Alert>
          )}
        </Col>
      </Row>

      <Row>
        {hasProfile && (
          <Col className="">
            <Item
              onClick={handleData}
              items={items}
              TemplateComponent={TemplateComponent}
              onSearch={(query) => queryApplication(query)}
            />
          </Col>
        )}
      </Row>
    </Container>
  );
};

export default IndexPage;

export const Head: HeadFC = () => <title>Home Page</title>;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
