import * as React from "react";
import type { HeadFC } from "gatsby";
import Layout from "../components/layout/Layout";
import { WrappedParse as Parse } from "../vendor/ParseApp";
import Table from "react-bootstrap/Table";
import { timeSince } from "../helpers/time";
import { handleLocation } from "../helpers/data";
import Search from "../page/Search";
import {
  Trans,
  useTranslation,
  I18nextContext,
} from "gatsby-plugin-react-i18next";
import { graphql } from "gatsby";
import Badge from "react-bootstrap/Badge";

const handleData = (key, e) => {
  if (key === "location") {
    return handleLocation(e);
  }

  if (Object.prototype.toString.call(e) === "[object Date]") {
    return timeSince(e);
  }

  if (typeof e === "string") {
    return e;
  }

  if (Array.isArray(e)) {
    return e.map((obj) => <Badge bg="secondary"> {obj?.label} </Badge>);
  }
};

const IndexPage = () => {
  const [items, setItems] = React.useState([]);
  const { t } = useTranslation();
  React.useEffect(() => {
    Parse.Cloud.run("searchApplication").then((items) => {
      setItems(items);
    });
    const url = "/assets/choices.js/public/assets/scripts/choices.min.js";
    let script = document.querySelector(`script[src="${url}"]`);
    script = document.createElement("script");
    script.type = "application/javascript";
    script.src = url;
    script.async = true;
    document.body.appendChild(script);
  }, []);

  return (
    <Layout>
      <section className="section">
        <Search />
      </section>
    </Layout>
  );
};

export default IndexPage;

export const Head: HeadFC = () => <title>Home Page</title>;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
